<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <div style="display: flex; height: 100%">
      <div class="back" style="width: 84%">
        <h1> Phonematon</h1>
        <p>Entrez votre texte ici :</p>
        <textarea v-model="test" v-on:change="changText"></textarea>
        <p>Et obtenez le résultat ici :</p>
        <div style="background-color: white" id="output"></div>
      </div>
      <filterManager @execute="makeWanted($event)" :phonemes="phonemesList" style="width: 24%" />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import filterManager from "@/components/filter/filterManager.vue";
import json from './data.json'

export default {
  name: "home",
  components: {
    filterManager
  },
  data() {
    return {
      test: "",
      phonemesList: json.phonemes,
      conditions: json.conditions
    };
  },
  methods: {
    changText(){
      localStorage.setItem("text", this.test)
    },

    testConditions(madeBy, text, currentIndex){
      // console.log("madeBy : ", madeBy)
      // console.log("text : ", text)
      // console.log("currentIndex : ", currentIndex)
      if(Object.keys(madeBy["conditions"]).length > 0){

        let keys = Object.keys(madeBy["conditions"]);
        let stackResult = []
        for(let k = 0; k < keys.length; k++){
          let key = keys[k]
          let currentCondition = this.conditions[key]
          let result = this.testList(madeBy["conditions"][key], text, currentIndex, currentCondition.needs, currentCondition.sense)
          // stackResult.push(result);
          // console.log("result: "+result);
          if(result == false){
             //console.log("yo")
             return false
          }
        }
        if(stackResult.length > 1){
          switch(madeBy.gate){
            case"or":
              return (stackResult[0] || stackResult [1] ) ? true : false
            case"and":
              return (stackResult[0] && stackResult [1] ) ? true : false
          }
        }else{
          return stackResult[0]
        }
      }
      return true
    },

      testList(list, text, currentIndex, needs, sense){
      //console.log(list);
      for(let i = 0; i < list.length; i++){
        let p = list[i]
        // console.log("p : ",p)
        // console.log("text.length : ", text.length)
        /*for(let l = 0; l < text.length; l++){
          console.log("letter : ", text[l])
        }*/
        // console.log("currentIndex : ", currentIndex)
        // Pour tout les son de la liste
        // si le(s) lettre(s) qui suivent la lettre font partie du son alors
        // console.log("needs: " + needs)
        // console.log("sense: " + sense)

        if(p === "`" && currentIndex === (text.length -1)){
          return false
        }
        // console.log("currentIndex : " + currentIndex)
        // console.log("sense : " + sense)
        // console.log("(sense ? -1 : p.length) : " + (sense ? -1 : p.length))
        // console.log("currentIndex - (sense ? -1 : p.length) : " + (currentIndex - (sense ? -1 : p.length)))
        // console.log(text.substr(currentIndex - (sense ? -1 : p.length), p.length))
        // console.log(p)
        if(text.substr(currentIndex - (sense ? -1 : p.length), p.length) === p){
          // console.log("ya")
          return needs
        }
      }
      return list.length > 0 ? !needs:needs;
      //console.warn("--- [ TESTLIST ] ---")
    },

    FindAllRest(tab, text) {
      console.warn("--- [ FINDALLREST : START ] ---")
      let nextWord = "";
      let currentIndex = 0;
      for (let l = 0; l < text.length; l++) {
        if (text[l] === "-") {
          if (nextWord.length > 0) {
            tab[currentIndex] = this.CreateSpan(nextWord, "000000");
          }
          currentIndex = l + 1;
          nextWord = "";
        } else {
          nextWord += text[l];
        }
      }
      if (nextWord.length > 0) {
        tab[currentIndex] = this.CreateSpan(nextWord, "000000");
      }
      console.warn("--- [ FINDALLREST : END ] ---")
      this.FillResult(tab);
    },

    ClearResult() {
      console.warn("--- [ CLEAR RESULT : START ] ---")
      let outputDiv = document.getElementById("output");
      // console.log(outputDiv)
      // console.log("outputDiv.childNodes.length : ", outputDiv.childNodes.length);
      // console.log("outputDiv.childElementCount : ", outputDiv.childElementCount);
      // console.log("outputDiv.hasChildNodes() : ", outputDiv.hasChildNodes());
      // console.log("doc.firstChild : ", outputDiv.firstChild);
      while (outputDiv.firstChild !== null) {
        // console.log("doc.firstElementChild : ", outputDiv.firstChild);
        outputDiv.removeChild(outputDiv.lastChild);
      }
      //console.log("childNodes : ", outputDiv.childNodes)
      console.warn("--- [ CLEAR RESULT : END ] ---")
    },

    FillResult(tab) {
      console.warn("Fill Result")
      console.log("childNodes : ", document.getElementById("output").childNodes)
      console.log("tab : ", tab)
      for (let i = 0; i < tab.length; i++) {
        if (tab[i] !== undefined) {
          document.getElementById("output").appendChild(tab[i]);
        }
      }
    },

    CreateSpan(text, color) {
      console.warn("--- [ CREATESPAN : START ] ---")
      // console.log("spanText : ", text)
      // console.log("spanColor : ", color)
      let span = document.createElement("span");
      span.innerText = text;
      span.style.color = color
      console.warn("--- [ CREATESPAN : END ] ---")
      return span;
    },

    makeWanted(event) {
      this.ClearResult();

      let tab = [];
      // console.log("text : ", text)
      let finalText = JSON.parse(JSON.stringify(this.test));
      // console.log("finalText : ", finalText)

      let filterPhonemeList = event
      // console.log("filterPhonemeList : ", filterPhonemeList);
      filterPhonemeList.forEach((phoneme, phonemeI) => {
        // console.log("currentPhoneme :", phoneme)
        let text = JSON.parse(JSON.stringify(this.test));
        text = text.split(' ').join('`');
        phoneme.madeBy.forEach((method, methodI) => {
          // console.log("currentMethod : ", method["with"], method["condition"])
          while(text.indexOf(method["with"]) > -1){
            let withIndex = text.indexOf(method["with"]);
            if(this.testConditions(method, text, withIndex)){
              // console.log("Condition Passed")
              // console.log("method.with :", method["with"])
              // console.log("Current State of text : ", text)
              // console.log("Current State of finalText : ", finalText)
              text = text.replaceAt(withIndex, ("-").repeat(method["with"].length))
              finalText = finalText.replaceAt(withIndex, ("-").repeat(method["with"].length))
              // console.log("Current State of text : ", text)
              // console.log("Current State of finalText : ", finalText)
              tab[withIndex] = this.CreateSpan(method["with"], phoneme["color"])
              // console.log("tab : ", tab);
            }else{
              // console.log("method.with :", method["with"])
              text = text.replaceAt(withIndex, ("-").repeat(method["with"].length))
              // console.log("Current State of text : ", text)
            }
          }
        })
      })
      // console.log("Current State of text : ", text)
      // console.log("Current State of finalText : ", finalText)
      // console.log("tab : ", tab);
      this.FindAllRest(tab, finalText)
    },
  },
  mounted() {
    // let aie = document.getElementById("output");
    // console.log("aie : ", aie);
    if(localStorage.getItem("text")!= undefined && localStorage.getItem("text")!= ""){
      this.test = localStorage.getItem("text")
    }
  }
};
</script>
<style>
.back {
  background-color: #dfdfdf;
}
#output {
  width: 90%;
  min-height: 150px;
  border: 1px solid black;
  margin: auto;
}
textarea {
  min-height: 150px;
  min-width: 90%;
  width: 90%;
  max-width: 90%;
}
.home {
  height: 94.6%;
  margin: 0px;
}
  h1{
    font-family: "Fredoka One";
  }
</style>
