import Vue from 'vue'
import Router from 'vue-router'

import Home from './views/Home.vue'
import Faq from "./views/Faq";
import About from "./views/About";


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/faq',
      name: 'foire au question',
      component: Faq
    },
    {
      path: '/about',
      name: 'about',
      component: About
    }
  ]
})
