<template>
  <div style="background-color: #f2f2f2; display: flex; flex-direction: column; justify-content: space-between">
    <div>
      <div class="title">
        <h1 class="TitleShadow">FILTRE</h1>
      </div>
      <div class="lister">
        <!-- @addItem="AddItem(itemInfos, index)" -->
        <item
          @askChangeDropDown="itemDropDownChange($event, index)"
          @removeItem="removeItem(index)"
          @askColorChange="itemColorChange($event, index)"
          :itemConfig="itemInfos"
          v-for="(itemInfos,index) in itemList"
          :key="itemInfos.id"
        />
        <div id="item">
          <button @click="addItem()" class="button success block" type="button" >
            <img alt="Vue logo" src="@/assets/plus_icon.png" />
          </button>
        </div>
      </div>
    </div>
    <button @click="Validate" class="validateButton">
      <h1 class="TitleButtonShadow">APPPLY</h1>
    </button>
  </div>
</template>
    
<script>
import item from "@/components/filter/item.vue";

export default {
  name: "filterManager",
  components: {
    item
  },
  props: {
    phonemes: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      itemList: [],
      selectedPhonemId: [],
      currentIndex: 0
    };
  },
  methods: {
    itemDropDownChange(object, index) {
      let currentItem = this.itemList[index];
      if (currentItem.currentlyUsed > 1) {
        let selectedId = currentItem.dropDownConfig.selectedId;
        this.itemListUseChecker(-1, selectedId, index);
      }
      currentItem.dropDownConfig.selectedId = object.option.id;
      currentItem.dropDownConfig.selectedIndex = object.index;
      this.itemListSameCounter(object.option.id);
    },
    itemColorChange(event, index){
      this.itemList[index].color = event.target.value
    },
    itemListUseChecker(value, idToSearch, indexToSkip) {
      for (let i = 0; i < this.itemList.length - 1; i++) {
        let element = this.itemList[i];
        if (
          i != indexToSkip &&
          element.dropDownConfig.selectedId == idToSearch
        ) {
          element.currentlyUsed += value;
        }
      }
    },
    itemListSameCounter(idToSearch) {
      let indexList = [];
      for (let i = 0; i < this.itemList.length - 1; i++) {
        let element = this.itemList[i];
        if (element.dropDownConfig.selectedId == idToSearch) {
          indexList.push(i);
        }
      }
      indexList.forEach(element => {
        this.itemList[element].currentlyUsed = indexList.length;
      });
    },
    addItem() {
      if (this.itemList.length < this.phonemes.length + 1) {
        this.itemList.push({
          id: this.itemList.length + 1,
          color: "#FF0000",
          dropDownConfig: {
            options: this.phonemes,
            selectedIndex: 0,
            selectedId: 0,
          }
        });
        // this.itemListSameCounter(0);
      }
      return true;
    },
    removeItem(index) {
      this.itemList.splice(index, 1);
    },
    Validate() {
      let indexList = []; // sécuriter pour verifier qu'on à pas deux fois le même phoneme
      let phonemesList = [];

      // console.log("this.itemList :", this.itemList);
      // console.log("this.itemList.length :", this.itemList.length);
      for (let i = 0; i < this.itemList.length; i++) {
        let currentItem = this.itemList[i];

        // console.log("currentItem :", currentItem);
        if (indexList.indexOf(currentItem.dropDownConfig.selectedId) === -1) {
          indexList.push(currentItem.dropDownConfig.selectedId);

          let currentPhoneme = this.phonemes[currentItem.dropDownConfig.selectedIndex]
          phonemesList.push({
            "color": currentItem["color"],
            "madeBy": currentPhoneme["madeBy"],
            "id": currentPhoneme["id"]
          });
        }
      }
      // console.log("phonemesList : ",phonemesList)
      this.$emit("execute", phonemesList);
    },

  },
  mounted() {
    console.warn("--- [ FILTER MANGER : MOUNTED ] ---")
    // console.log("this.phonemes :",this.phonemes);
  }
};
</script>

