<template>
    <div>
        <div class="currentOption">
            <div v-if="currentOption != null" style="display: flex; justify-content: space-around">
                <div class="currentDropdownOption">{{currentOption.notation}}</div>
                <div @click="toggleDropDown()">▼</div>
            </div>
        </div>
        <div class="dropdown" v-if="dropdownOn">
            <div class="dropdownOption" v-for="(option, index) in config.options"
                 @click="selectOption(index); $emit('setSelectedOption',{'option':option,'index':index})">{{option.notation}}</div>
        </div>
    </div>
</template>

<script>
export default {
    name: "DropDown",
    props: {
        config:{
            required: true
        }
    },
    data() {
        return {
            dropdownOptions: [
                "A",
                "B",
                "C",
                "D",
                "E",
                "F",
                "G",
                "H",
                "I",
                "J",
                "K"
            ],
            dropdownOn: false,
            currentOption: null
        }
    },
    methods:{
        toggleDropDown(){
            this.dropdownOn = !this.dropdownOn
        },
        selectOption(optionIndex){
            this.config.selectedIndex = optionIndex
            this.config.selectedId = this.config.options[optionIndex].id
            this.currentOption = this.config.options[optionIndex]
            this.dropdownOn = false
        }
    },
    mounted() {
        this.currentOption = this.config.options[this.config.selectedIndex]
    }
}
</script>


<style scoped>
    .dropdown{
        background-color: violet;
        border-radius: 5px;
        /*height: 50px;*/
        width: 175px;
        max-height: 200px;
        overflow-y: auto;
    }
    .currentOption{
        background-color: #DDD;
        border-radius: 5px;
        height: 50px;
        width: 175px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: 5px 0px;
        font-size: 25px;
        font-weight: bold;
        /*font-family: "Fredoka One";*/
    }
    .dropdownOption:nth-child(even){
        background-color: #DDD;
    }
    .dropdownOption:nth-child(odd){
        background-color: #CCC;
    }
    .dropdownOption{
        padding: 10px;
        font-size: 25px;
        font-weight: bold;
        /*font-family: "Fredoka One";*/
    }
</style>